export default {
    props: ['modalOptions'],
    computed: {
        modal() {
            let modal = this.modalOptions;
            modal = {
                title: modal.title || '',
                text: modal.text,
            };
            return modal;
        },
    },
    data() {
        return {
            show: false, // 
            resolve: '',
            reject: '',
            promise: '', // promise
        };
    },
    methods: {
        submit() {
            this.resolve('submit');
        },
        close(type) {
            this.show = false;
            this.reject(type);
        },
        confirm() {
            this.show = true;
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise; //promise,
        },
    }
}



